import moment from "moment";

export function Today(props) {
    return (
        <div className="App-today">
            <p>
                 {moment().format("ll")},{" "}
                {moment().format("dddd")}
            </p>
            {props.CurrentWeek % 2 !== 0 ? (
                <p className="Week_1">
                    {props.CurrentWeek}  
                </p>
            ) : (
                <p className="Week_2">
                    {props.CurrentWeek}  
                </p>
            )}
        </div>
    );
}
